import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
// import { NgbDatepickerConfig, NgbCalendar, NgbDate, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'nz-form-builder',
  templateUrl: './form-builder.component.html',
  styleUrls: ['./form-builder.component.scss']
})
export class FormBuilderComponent implements OnInit {

  @Input() fieldConfig: any;
  @Input() formConfig: any;
  @Input() journey: string;
  @Input() dataToUpdate: any;

  @Output() formValues: any = new EventEmitter()

  checkboxArr = [];
  filesSelected: any;
  dynamicForm: FormGroup;

  constructor(
    private fb: FormBuilder,
  ) { }

  ngOnInit() {
    this.createDynamicForm();
  }

  /**
   * Create Form Group
   */
  createDynamicForm() {
    this.dynamicForm = this.fb.group({}),
      this.dynamicForm.valueChanges.subscribe((value) => {
      })
    this.initializeDynamicForm();
    // dummy data to test
    const valuesDyn = {
      "firstname": "nizam123",
      "lastname": "shaik456",
      "class": "8",
      "age": "30",
      "dateofbirth": "1994-12-05",
      "gender": "female",
      "state": [
        {
          "key": "Nellore",
          "value": "nellore",
          "isChecked": true
        }
      ],
      "address": "buchi reddy palem",
      "file": {
        "0": {}
      }
    }
    if (this.journey === 'update' && this.dataToUpdate){
      this.setFormFields(this.dataToUpdate);
    }
      
  }

  /**
   * Initialize the form with all validations and patterns
   */
  initializeDynamicForm() {
    this.fieldConfig.forEach((row) => {
      if (row.required === true && row.pattern != '') {
        this.dynamicForm.addControl(row.name, new FormControl({ value: '', disabled: row.disabled }, [Validators.required, Validators.pattern(row.pattern)]));
      } else if (row.pattern != '') {
        this.dynamicForm.addControl(row.name, new FormControl({ value: '', disabled: row.disabled }, [Validators.pattern(row.pattern)]));
      } else if (row.required === true) {
        this.dynamicForm.addControl(row.name, new FormControl({ value: '', disabled: row.disabled }, [Validators.required]));
      } else {
        this.dynamicForm.addControl(row.name, new FormControl({ value: '', disabled: row.disabled }));
      }
    });
  }

  /**
   * For Validations , This marks all fields as touched on click of submit
   */
  private markFormGroupTouched(formGroup: FormGroup) {
    (<any>Object).values(formGroup.controls).forEach(control => {
      control.markAsTouched();
      if (control.controls) {
        this.markFormGroupTouched(control);
      }
    });
  }

  onCancel() {
    // reset the form
    Object.keys(this.dynamicForm.controls).forEach((name, i) => {
      this.dynamicForm.controls[name].patchValue('');
    })
  }

  /**
   * Checkbox logic
   */
  toggleSelect(event, option, field, actualArr) {

    const found = this.checkboxArr.some((el) => {
      return el.key === option.key;
    });
    if (!found) {
      this.checkboxArr.push({ key: option.key, value: option.value, isChecked: true });
    } else {
      const index = this.checkboxArr.map((row) => { return row.key; }).indexOf(option.key);
      this.checkboxArr.splice(index, 1);
    }
    this.dynamicForm.controls[field.name].setValue(this.checkboxArr);
  }

  /**
   * The below logic for edit Journey
   * @param values => will be the form values to patch in form
   */

  setFormFields(values) {
    this.checkboxArr = [];
    Object.keys(values).forEach((name) => {
      if (this.dynamicForm.get(name)) {
        this.dynamicForm.controls[name].patchValue(values[name]);
      }
    })

    // If checkbox exist in field Config , the below block activates and map the values to checkbox
    this.fieldConfig.forEach(element => {
      if (element.type === 'checkbox') {

        const selectedOptions = this.dynamicForm.get(element.name).value;
        const totalOptions: any = element.options;
        this.checkboxArr = selectedOptions;

        totalOptions.forEach((r) => { r.isChecked = false; })

        for (let i = 0; i < selectedOptions.length; i++) {
          const index = totalOptions.map((row) => { return row.value; }).indexOf(selectedOptions[i].value);
          totalOptions[index].isChecked = true;
        }
      }
    });
  }

  /**
   * For Input type File
   */
  onFileSelected(event, field) {
    this.filesSelected = event.target.files;
    this.dynamicForm.controls[field.name].setValue(event.target.files);
  }

  /**
   * On Submit form
   */
  onDSubmit(dynamicForm) {
    this.markFormGroupTouched(this.dynamicForm);
    this.formValues.emit(this.dynamicForm);
  }

  // Branding
  mailTo() {
    let url;
    url = 'mailto:' + 'nizamuddin407@gmail.com';
    url += '?subject=' + 'Dynamic form builder';
    url += '&body=' + '';
    window.open(url, '', '"width=200,height=100"');
  }

}
